            }
            let params = new FormData();
            params.append('data', JSON.stringify(data))
            
            
            var files = e.target.files;
            
            for(var i=0; i<files.length; i++){
            	params.append('file'+i, files[i]);
            }
            
            
            
            
            
            
            
            
            
            
